<template>

    <!-- main wrapper -->
    <main class="wrapper">

        <div class="divider"></div>

        <!-- init slide screen -->
        <div class="row noflex fullheight">

            <!-- floating triangles (temporarily disabled) -->
            <div class="shape triangle" v-if="false"></div>
            <div class="shape triangle" v-if="false"></div>

            <!-- cover with title -->
            <div class="cover">
                {{ cover }}
            </div>

        </div>

        <!-- second screen slide -->
        <div class="row">

            <div class="inner">

                <!-- method picker -->
                <div class="choose">
                    <span @click.prevent="changeMethod('GET')" v-on:keyup.enter="changeMethod('GET')" :class="{selected: getActive}" tabindex="0">GET</span>
                    <span @click.prevent="changeMethod('POST')" v-on:keyup.enter="changeMethod('POST')" :class="{selected: postActive}" tabindex="0">POST</span>
                </div>

                <!-- method items -->
                <div class="container" v-for="(item, idx) in this.feed[method].items" :class="item.sideInfo" :key="item.uniq">
                    
                    <Item :title="item.title" :description="item.description" :sideInfo="item.sideInfo" @activated="handle" :style="{zIndex:(feed[method].items.length-idx)}" v-if="item.sideInfo === method" :tabindex="0"></Item>
                
                    <!-- dynamic hover container -->
                    <div class="stroker" v-if="item.params">

                        <!-- curve -->  
                        <svg width="190" height="160" xmlns="http://www.w3.org/2000/svg" viewbox="0 0 100 50">
                            <path d="M 0, 50 C 100, 50, 0, 0, 100, 0" stroke="black" fill="transparent"/>
                        </svg>

                        <!-- box -->
                        <div class="params">
                            {{ item.params }}
                        </div>
                    </div> 
               
                </div>
            </div>

            <!-- floating display -->
            <div class="inner info">
                <div class="textfield">
                    <div class="display" :aria-label="this.text" tabindex="0">{{ this.text }}</div>
                </div>
            </div>

        </div>
  
    </main>
</template>

<script>

    import Item from '@/components/Item.vue'
    import feed from '@/feed.js'

    export default {
    
        name: 'home',

        components: {
            Item
        },

        data() {
            return {
                feed: feed,
                text: 'API',
                cover: process.env.VUE_APP_TITLE,
                method: 'POST'
            }
        },

        computed: {

            /**
             * Find if GET method is active.
             * 
             * @returns Flag if GET method is active
             */
            getActive() { 
                return this.method === "GET"
            },

            /**
             * Find if POST method is active.
             * 
             * @returns Flag if POST method is active
             */
            postActive() { 
                return this.method === "POST"
            },
        },

        methods: {

            /**
             * Set text value.
             * 
             * @param value - Handle emitted value
             */
            handle(value) {
                this.text = value;
            },

            /**
             * Invokes callback over selected elements.
             * 
             * @param selector - Class of selected items
             * @param callback - Function to execute
             */
            groupSelector(selector, callback) {
                const elements =  document.getElementsByClassName(selector);

                for(let el of elements) {
                    callback(el);
                }
            },

            /**
             * Changes class of elements selected by class name.
             * 
             * @param selector - Class name of selected elements
             * @param cls - New class name
             */
            changeGroupClass(selector, cls) { 
                this.groupSelector(selector, (el) => {
                    el.className = cls;
                });
            },

            /**
             * Change CSS attribute of selected elements.
             * 
             * @param selector - Class name of selected elements
             * @param property - CSS property name
             * @param value - CSS prop value to set
             */
            changeGroupCss(selector, property, value) {
                this.groupSelector(selector, (el) => {
                    el.style[property] = value;
                });        
            },

            /**
             * Change method.
             * 
             * @param method - HTTP method
             */
            changeMethod(method) {
                
                if (this.method === method){ return; }

                this.changeGroupClass('item', 'item fadeOutRight animated');
                this.changeGroupCss('textfield', 'opacity', 0);

                setTimeout(() => {
                    this.changeGroupClass('item', 'item fadeInLeft animated');
                    this.changeGroupCss('textfield', 'opacity', 1);
                    this.method = method
                }, 1000);
            }
        },

        /** mounted() {
            
            // Target our triangles.
            let origins = [ document.getElementsByClassName('triangle')[0].offsetTop, 
                            document.getElementsByClassName('triangle')[1].offsetTop ];

            // Make triangles float!
            window.onscroll = () => {
                
                if (document.getElementsByClassName('divider')[0].offsetHeight > 0) {
                    window.onscroll = '';
                }
            
                document.getElementsByClassName('triangle')[0].style.top = origins[0] + window.scrollY/3 + 'px';
                document.getElementsByClassName('triangle')[0].style.transform = 'rotate(' + (-20 + (window.scrollY/50)) + 'deg)';
                document.getElementsByClassName('triangle')[1].style.top = origins[1] + window.scrollY/10 + 'px';
            }
        } */
    }

</script>

<style lang="scss">

    @for $i from 1 through 9 {
        .inner .container:nth-child(#{$i}) .item {
            animation-delay: $i/15 * 1s;
        }
    }

    .home {
        width:100%;
        height: 100%;
    }

    html {
        width: 100%;
        height: 100%;
        margin: 0;
        font-size: 20px;
        font-display: swap;
    }

    body {
        background-color: $background;
        width: 100%;
        height: 100%;
        margin: 0;
        overflow-x: hidden;

        svg {

            path {
                stroke: $foreground;
                stroke-dasharray: 1000;
                stroke-dashoffset: 1000; 
            }

            @keyframes dash {
                from {
                    stroke-dashoffset: 1000;
                }
                to {
                    stroke-dashoffset: 0;
                }
            }
        }
    }

    .wrapper {

        font-family: 'Open Sans', sans-serif;
        text-align: center;
        height: 100%;

        .row {
            border: 3px solid $border;

            &:not(.noflex) {
                display: flex;
            }

            &.fullheight {
                height: calc(100% - 6px);
            }

            .shape {
                transition: top 1s, transform 1s;

                &.triangle {
                    position: absolute;
                    display: none;
                    width: 0;
                    height: 0;
                    border-bottom: 500px solid $lighter;
                    border-right: 500px solid transparent;
                    transform: rotate(-20deg);
                    z-index: -1;
                    top: -100px;

                    &:not(:first-child) {
                        right: 0;
                        bottom: -100px;
                        top: auto;
                        transform: rotate(120deg);
                    }
                }
            }
        }

        .cover {
            position: relative;
            top: 50%;
            transform: translateY(-50%);
            color: $foreground;
            font-size: 3rem;
        }

        .inner {
            flex: 50%;
            color: $foreground;
            font-weight: normal;

            .container {
                position: relative;

                .stroker {
                    position: absolute;
                    left: 19rem;
                    bottom: calc(50% - .5rem);
                    animation: none;
                    z-index: 998;

                    .params {
                        outline: 2px solid $border;
                        border: 5px solid $lighter;
                        position: absolute;
                        left: calc(100% + .5rem);
                        white-space: nowrap;
                        z-index: 999;
                        top: -.5rem;
                        transition: opacity .3s;
                        opacity: 0;
                        padding: .5rem;
                        font-size: .7rem;
                        background-color: $background;
                    }

                    svg {
                        width: 100px;
                        height: 50px;
                    }
                }
            }

            .choose {
                margin-bottom: 2rem;
                font-size: 70%;
                user-select: none;
                pointer-events: inherit;

                span {
                    background-color: $background;
                    transition: background-color .3s;
                    user-select: none;
                    font-weight: bold;

                    &.selected {
                        background-color: $border;
                        color: $background;
                    }

                    &:first-child {
                        margin-right: .5rem;
                    }

                    &:hover {
                    }

                    padding: .5rem .8rem;
                    border: 2px solid $border;
                    border-radius: 5px;
                    cursor: pointer;
                }
            }

            .textfield {
                position: relative;
                top: calc(50% - 2.5rem - 5px);
                color: $foreground;
                font-weight: bold;
                font-size: .9rem;
                animation: float 10s ease-in-out infinite;
                background-color: $lighter;
                padding: 2rem;
                border: 5px solid $border;
                border-radius: 5px;
                transition: opacity .3s;

                .display {
                    text-overflow: ellipsis;
                }
            }
        }

        & .inner:first-child {
            word-wrap: break-word;
            flex-direction: column;
            text-align: left;
            padding: 1rem;
            border-right: 3px solid $border;
            background-color: $background;
            min-width: 18rem;
        }

        & .inner.info {
            background-color: $background;
            padding: 1rem;
            flex: 25%;
        }
    }

    @keyframes float {
        0% {
            transform: translateY(0px) rotate(5deg);
            box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
        }
        50% {
            box-shadow: 0 5px 15px 0px rgba(0,0,0,0.4);
            transform: translateY(20px) rotate(5deg);
        }
        100% {
            box-shadow: 0 25px 15px 0px rgba(0,0,0,0.2);
            transform: translateY(0px) rotate(5deg);
        }
    }

    @media only screen and (max-width: 1000px) {

        .inner:first-child {
            border-right: none;
            overflow-x: hidden;
        }

        .inner.info {
            display: none;
        }

    }

    @media only screen and (max-width: 736px) {

        .wrapper {

            .divider {
                width: 2px;
                height: 3100px;
                background-color: $foreground;
                position: absolute;
                left: 50%;
                top: calc(100% + 200px);
                display: none;
            }

            .row {

                &:not(.noflex) {
                    display: block;
                    margin-top: -1px;
                }

                .shape.triangle {
                    display: none;
                }

                .cover {
                    font-size: 14vw;
                }
            }

            .inner {
                display: flex;
                justify-content: center;
                align-items: center;
                border: none;
                min-width: auto;

                .stroker {
                    display: none;
                }

                .choose {
                    margin-top: 2rem;
                }
                
                &:first-child {
                    padding: 0;
                    border-right: 3px solid $border;
                    min-width: auto;
                    overflow-x: hidden;
                }

                .container {
                    width: 100%;
                }

                &.info {
                    padding: 0;
                }

                > span {
                    font-size: 1.5rem;
                }

                .textfield {
                    display: none;
                }

                &:first-child {
                    border: none;
                }
            }
        }
    }

</style>