<template>

    <!-- main wrapper -->
    <div class="item fadeInLeft animated" @click="release($event.currentTarget)" v-on:keyup.enter="release($event.currentTarget)" :data-before-content="this.sideInfo">
      
        <!-- text container -->
        <div class="texts">

            <!-- title -->
            <div class="title">
                {{ this.title }}
            </div>
        
            <!-- description -->
            <div class="description">
                {{ this.description }}
            </div>

        </div>

    </div>

</template>

<script>

    export default {

        name: 'Item',

        props: {
            title: String,
            description: String,
            sideInfo: String,
            param: String
        },

        methods: {

            /**
             * Emit event after clicking.
             *
             * @param target - HTML node that was clicked
             */
            release(target) {
                this.$emit('activated', 'jsonstream.herokuapp.com' + (target.children[0].children[0].innerHTML).trim());
            }
        }
    }

</script>

<style lang="scss">

    $floater_offset: 101%;

    .item {
        color: $foreground;
        padding: 1rem;
        border-radius: 5px;
        width: 300px;
        cursor: pointer;
        position: relative;
        box-shadow: 0 10px 20px rgba(0,0,0,.19), 0 6px 6px rgba(0,0,0,.1);
        transition: left 0.3s, box-shadow 0.3s, color 0.3s;
        text-align: center;
        left: 0;
        z-index: 100;
        overflow-x: hidden;
        background-color: $background;
        margin-top: -3px;

        &:before {
            content: attr(data-before-content);
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate(-($floater_offset), -50%);
            transition: transform 0.7s, color .3s;
            font-weight: bold;
            font-size: 110%;
            color: $foreground;
        }

        &.item:hover ~ .stroker, &.item:focus ~ .stroker {
            svg path {
               animation: dash 4s linear forwards;
            }

            .params {
                opacity: 1 !important;
            }
        }

        &:hover:before, &:focus:before {
            opacity: 1;
            transform: translate($floater_offset/5, -50%);
            color: $outline;
        }

        &:hover, &:focus {
            left: 2rem;
            box-shadow: none;
            color: $outline;
        }

        .title {
            text-align: center;
        }

        .param {
            margin-top: .5rem;
            font-size: .7rem;
        }

        .description {
            margin-top: .5rem;
            font-size: .7rem;
        }
    }

    @media only screen and (max-width: 736px) {

        .item {
            width: 100%;
            height: 400px;
            display: flex;
            justify-content: center;
            align-items: center;
            box-shadow: none;
            padding-left: 0;
            padding-right: 0;
            border-radius: 0;
            pointer-events: none;

            &:before {display: none;}
            &:hover:before, &:focus:before {pointer-events: none;}

            &:hover, &:focus {
                left: 0;
            }

            .texts {
                background-color: $background;
                padding: 3rem;
                border-radius: 20px;
                box-shadow: 0 10px 20px rgba(0,0,0,.19), 0 6px 6px rgba(0,0,0,.1);
            }
        }
    }

</style>
