<template>
  
    <div id="app" :class="this.$router.currentRoute.name" class="fadeIn animated">
        <router-view/>
    </div>
  
</template>

<script>

    export default {

        methods: {

            /**
             * Method for SW worker registering.
             */
            swRegister() {
                if ('serviceWorker' in navigator) {
                    window.addEventListener('load', () => {
                        navigator.serviceWorker.register('/service-worker.js');
                    });
                }
            }
        },

        mounted() {
            this.swRegister();
        }
    }

</script>

